.banner-container {
  position: relative;
  // width: 100%;
  // height: 632px;
  box-sizing: border-box;
  margin: 0px 12px;
  overflow: hidden;

  .banner-section-wrapper {
    position: relative;
    width: 100%;
    height: 632px;

    .banner-content-wrapper {
      position: absolute;
      // height: 100%;
      // width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      .banner-content {
        // width: 50%;
        p {
          text-align: center;
          font-weight: 600;
          font-size: 96px;
          line-height: 98px;
        }

        .italic-content {
          font-weight: 400;
        }
      }
    }
  }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .banner-container {
    .banner-section-wrapper {
      .banner-content-wrapper {
        .banner-content {
          p {
            font-size: 80px;
            line-height: 82px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .banner-container {
    .banner-section-wrapper {
      .banner-content-wrapper {
        .banner-content {
          p {
            font-size: 40px;
            line-height: 42px;
          }
        }
      }
    }
  }
}
